<template>
  <div id="app">
    <Swap :hasMetamask="hasMetamask" :account="account" :chainId="chainId"/>
    <RadialGradient/>
  </div>
</template>

<script>
import {bus} from "./main"
import Swap from "@/components/Swap";
import RadialGradient from "@/components/RadialGradient";

export default {
  name: 'App',
  components: {RadialGradient, Swap},
  created() {
    bus.$on('connect-wallet', this.connectMetamask)
    window.ethereum.on('chainChanged', (chainId) => {
      this.chainId = chainId
      bus.$emit('chain-changed', chainId)
    });
    window.ethereum.on('accountsChanged', (accounts) => {
      this.account = accounts[0]
    });
  },
  beforeDestroy() {
    bus.$off('connect-wallet', this.connectMetamask)
  },
  data: function () {
    return {
      hasMetamask: typeof window.ethereum !== 'undefined',
      account: "",
      chainId: ""
    };
  },
  methods: {
    connectMetamask: function () {
      window.ethereum.request({method: 'eth_requestAccounts'})
          .then(accounts => this.step2(accounts))
          .catch(err => console.log(err))
    },
    step2: function (accounts) {
      this.account = accounts[0]
      window.ethereum.request({method: 'eth_chainId'})
          .then(chainId => this.step3(chainId))
          .catch(err => console.log(err))
    },
    step3: function (chainId) {
      this.chainId = chainId
      bus.$emit('wallet-connected')
      bus.$emit('chain-changed', chainId)
    }
  }
}
</script>

